<template>
  <div class="view column">
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="goTo('add')">新增</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <el-collapse-item :title="'个人列表'" :name="'个人列表'">
          <c-datagrid
            :tableData="[]"
            :tableHeader="[]"
            :info="APP.modelMainListDataGrid"
            @currentPageChange="handleCurrentPageChange"
          >
            <el-table
              :data="APP.modelMainList"
              style="width: 100%"
              height="400"
              @row-dblclick="handleClickTableRow"
            >
              <template v-for="(tableH, index) in APP.dslTableHeader">
                <el-table-column
                  v-if="!tableH.type"
                  :class="'t-column-'+index"
                  :prop="tableH.prop"
                  :label="tableH.label"
                  :width="tableH.width"
                  :key="index"
                ></el-table-column>

                <!-- 操作 -->
                <el-table-column
                  v-else-if="tableH.type===TYPE.BUTTON_UPDATE_DELETE"
                  :prop="tableH.prop"
                  :label="tableH.label"
                  :width="tableH.width"
                  :key="index"
                >
                  <template slot-scope="scope">
                    <div class="oprations">
                      <el-button @click="handleButtonUpdate(scope.row,scope)" icon="el-icon-edit"></el-button>
                      <el-button @click="handleButtonDelete(scope.row,scope)" icon="el-icon-delete"></el-button>
                    </div>
                  </template>
                </el-table-column>
                <!-- 操作 -->
                <el-table-column v-else :prop="tableH.prop" :label="tableH.label" :key="index"></el-table-column>
              </template>
            </el-table>
          </c-datagrid>
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";

export default {
  name: "FnbpcFkjc",
  inject: ["APP"],
  mixins: [Main],
  mounted() {
    this.activeNames.push("个人列表");
    console.log("this.APP.dslTableHeader", this.APP.dslTableHeader);
    /* 模拟获取数据 */
    this.APP.getMainList();
  },
  data() {
    return {
      /* 动态数据 */
      activeNames: []
    };
  },
  methods: {
    goTo(command) {
      this.$router.push({
        name: command
      });
    },
    handleCurrentPageChange(currentPage) {
      this.APP.modelMainListDataGrid.page = currentPage;
    },
    handleClickTableRow(row, column, cell, event) {
      console.log("row, column, cell, event\n", row, column, cell, event);
      this.APP.modelDetail = row;
      this.goTo("detail");
    }
  },
  computed: {}
};
</script>

