<template>
  <div id="app">
    <transition name="silde-fade">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import { model, dslArray, rules } from "./mock.js";
import { dslTableHeader } from "./mock.js";
/* 新增个人资料信息 */
import { modelGRZL, dslArrayGRZL, rulesGRZL } from "./mock.js";
export default {
  name: "AppRoot",
  provide() {
    return {
      APP: this
    };
  },
  data() {
    return {
      model,
      modelMainList: [],
      modelAdd: {},
      modelUpdate: {},
      modelDetail: modelGRZL,
      dslArray,
      rules,
      /* 个人信息列表 */
      dslTableHeader,
      modelMainListDataGrid: {
        page: 1,
        size: 10,
        total: 0
      },
      /* 新增个人资料信息 */
      modelGRZL,
      dslArrayGRZL,
      rulesGRZL
      /* 新增个人资料信息 */
    };
  },
  methods: {
    async getMainList() {
      let pageInfo = this.modelMainListDataGrid;
      console.log("getMainList", pageInfo);
      try {
        let { isSuccess, data } = await this.$post("/proxy/get", {
          coll: "fnbpc_jbzl" /* collection */,
          doc: {},
          pageInfo
        });
        if (isSuccess) {
          this.modelMainList = data;
          /* data 应该还有total一类的信息
           * 只是表示需要total
           */
          this.modelMainListDataGrid.total = data.length;
        }
      } catch (error) {
        console.log(error);
      }
    }
  },
  watch: {
    "modelMainListDataGrid.page": function(newPage, oldPage) {
      console.log("newPage, oldPage", newPage, oldPage);
      this.getMainList();
    }
  }
};
</script>

