<template>
  <div class="view column">
    <main class="main">
      <div class="tool-bar fixed">
        <template v-if="currentPath==='add'">
          <el-button @click="goTo('home',true)">放弃编辑</el-button>
          <el-button @click="save" type="primary" class="button">保存</el-button>
        </template>
        <template v-if="currentPath==='detail'">
          <el-button @click="goTo('home')">返回列表</el-button>
          <el-button @click="goTo('update')" type="primary" class="button">修改</el-button>
          <el-button @click="goTo('remove')" type="primary" class="button">删除</el-button>
        </template>
        <template v-if="currentPath==='update'">
          <el-button @click="goTo('detail',true)">放弃编辑</el-button>
          <el-button @click="save" type="primary" class="button">保存</el-button>
        </template>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <el-collapse-item :title="title" :name="'个人列表'">
          <c-table
            class="box-card"
            v-model="currentModel"
            :dsl="APP.dslArrayGRZL[0].rows"
            :rules="APP.rulesGRZL"
            :tableInfo="APP.dslArrayGRZL[0]"
            :disabled="isDisabled"
            ref="topTabe"
          />
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";

export default {
  name: "FnbpcFkjc",
  inject: ["APP"],
  mixins: [Main],
  mounted() {},
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.warn("beforeRouteEnter", to.name);
      vm.activeNames = ["个人列表"];
      let command = to.name;
      const initCurrentModel = {
        add() {
          /* 置空 */
          vm.currentModel = (model => {
            let obj = {};
            for (let key in model) {
              obj[key] = "";
            }
            return obj;
          })(vm.APP.modelDetail);
          console.log(vm.currentModel);
        },
        detail() {
          /* 跳转前已赋值 */
          vm.currentModel = JSON.parse(JSON.stringify(vm.APP.modelDetail));
        },
        update() {
          /* 初始化新增model */
          vm.currentModel = JSON.parse(JSON.stringify(vm.APP.modelDetail));
        }
      };

      initCurrentModel[command] && initCurrentModel[command]();
    });
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
  },
  beforeRouteUpdate(to, from, next) {
    next();
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
  },
  beforeRouteLeave(to, from, next) {
    next();
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
  },
  data() {
    return {
      activeNames: [],
      currentModel: {}
    };
  },
  methods: {
    goTo(command, isConfirm) {
      if (command === "remove") {
        this.$confirm(`确实要删除当前的表单?`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            const loading = this.$loading();
            /* 发送this.currentModel到后台处理删除请求 */
            console.log(this.currentModel);
            setTimeout(() => {
              this.afterRemove(loading);
            }, 1000 * 2);
          })
          .catch(error => {
            console.log("cancel", error);
          });
      } else {
        if (isConfirm) {
          this.$confirm(`确实离开?`, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.$router.push({
                name: command
              });
            })
            .catch(error => {
              console.log("cancel", error);
            });
        } else {
          this.$router.push({
            name: command
          });
        }
      }
    },
    async save() {
      console.log(this.APP.currentModel);
      function isOk(table) {
        if ("object" === typeof table) {
          return table.valid();
        }
      }
      const loading = this.$loading();
      try {
        /* 验证表单是否都符合rule */
        let isAllOk = await isOk(this.$refs.topTabe);
        let doc = this.currentModel;
        if (isAllOk) {
          /* save */
          let response = await this.$post("/proxy/save", {
            coll: "fnbpc_jbzl" /* collection */,
            doc /* document */
          });
          if (response.isSuccess) {
            /* 如果是修改，需要更新currentModel */
            if (this.$route.name === "update") {
              this.APP.modelDetail = doc;
              /* 刷新列表 */
              this.APP.getMainList();
            }
            loading.close();
            this.$router.push({ name: "detail" });
            this.$message({
              showClose: true,
              message: "保存成功",
              type: "success"
            });
          } else {
            console.log(response.data);
            throw new Error("error");
          }
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
      }
      loading.close();
    },
    afterRemove(loading) {
      /* 置空 */
      this.APP.model = {};
      /* 展开面板为查询 */
      this.activeNames = ["query"];
      loading.close();
      this.goTo("home");
      this.$message({
        showClose: true,
        message: "删除成功",
        type: "success"
      });
      /* 刷新列表 */
      this.APP.query();
    }
  },
  computed: {
    currentPath() {
      return this.$route.name;
    },
    isDisabled() {
      return this.currentPath === "detail";
    },
    title() {
      const titleList = {
        update: "更新",
        add: "新增",
        detail: "详情"
      };
      return "个人信息——" + titleList[this.currentPath];
    }
  }
};
</script>